import CompanyHead from '@/components/SearchCompany/head.vue'
import CompanyBaseInfo from '@/components/SearchCompany/baseinfo.vue'
import axios from 'axios'
 export default {
   data(){
     return{
       message:{},
     }
   },
   components: {
    CompanyHead,
    CompanyBaseInfo,
   },
   methods:{
     getConpanyInfo(){
       axios.get('http://rap2api.taobao.org/app/mock/22289/112')
         .then(this.getConpanyInfoSucc)
     },
     getConpanyInfoSucc(res){

         let item
         res = res.data
         const data = res.data
         for(item of data) {
         if(item.id==this.$route.params.id){
            this.message=item.details
            console.log(this.message.dongjiangao)
         }
           }
     }
   },
   mounted (){
     this.getConpanyInfo()

   },

 }



