
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import index from "../../router/index";
const list = () => ['法人/其他组织', '自然人']
export default {
props: {
company: Array,
people:Array
},
components: {
Tab,
TabItem,
Sticky,
Swiper,
SwiperItem,
Scroller,
Group,
Cell

},

data () {
return {
index01: 0,
list2: list(),
demo2: '法人/其他组织',
index: 0,
info:[]
}
},
mounted(){
this.$watch('company', function(newVal, oldVal){
this.info=newVal
})
},
updated(){

if (this.index==0){
this.info=this.company


}else{
this.info=this.people

}
}


}



