
import MyHeader from '@/components/base/mytop'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: "link-details",
components:{
Group,
Cell,
MyHeader
},
data(){
return{
company:'',
type:'',
state:''
}
},









methods:{
getData(){
var index = (this.$route.params.id);
console.log("路由传参"+index);

this.axios.get("http://rap2api.taobao.org/app/mock/22289/getLinkInfo").then((res)=>{
var data = res.data['linklistData'];

this.company=data[index].company
this.type = data[index].relativetype
this.state = data[index].state
})

}
},
mounted(){
this.getData()
}
}



