import DatabaseHead from '@/components/search/head.vue'
import axios from 'axios'
export default {
  data(){
    return{
      company:[],
      people:[],
      message:[],
    }
  },
  components: {
    DatabaseHead,
  },methods:{
    getDatabaseInfo(){
      axios.get('http://rap2api.taobao.org/app/mock/22289/200')
        .then(this.getDtabaseInfoSucc)
    },
    getDtabaseInfoSucc(res){
      res = res.data
      if (res.data){
        const message = res.data
        this.message=message

      }
    }
  },
  mounted (){
    this.getDatabaseInfo()
  },
}



