
import MyHead from '@/components/base/mytop.vue'
import MyTab from '@/components/linklist/mytab.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default {
data(){
return{
inputValue:'',
titleShow: true,
inputShow: false,
company:[],
people:[],
}
},
components: {
MyTab,
MyHead,
XInput

},methods:{
startInput(){
console.log("切换");


},
goSearch(){
this.$router.push({ path:'/querylist'})
},

getDatabaseInfo(){
this.axios.get('http://rap2api.taobao.org/app/mock/22289/linklistInfo')
.then(this.getDtabaseInfoSucc)
},
getDtabaseInfoSucc(res){
if (res.data){
this.company =res.data.linklistData;
this.people =res.data.linklistData2;
}
},

},

mounted (){
this.getDatabaseInfo()
},
}



