import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'

import LinkPay from '@/components/linkpay/LinkPay'
import QueryInfo from '@/components/linklist/QueryInfo'
import PayInfo from '@/components/linkpay/PayInfo'
import LinkDetails from '@/components/linklist/LinkDetails'
import LinkList from '@/components/linklist/Linklist'

import Work from '@/components/work/work.vue'
import Company from '@/components/company/company.vue'
import Database from '@/components/database/database.vue'

import LinkListself from '@/components/linklist/LinkListself.vue'
//si
import SearchVue from '@/components/SearchVue'
//shuai
import Search from '@/components/search/Search.vue'

import SearchCom from '@/components/SearchCompany/company.vue'


Vue.use(Router)


const  routes = [
    {
      path: '/',    //主页
      name: 'home',
      component: Home
    },

      {
          path: '/linkpay', //关联交易
          name: 'pay',
          component: LinkPay,

      },
    {
        path: '/payinfo',  //关联信息
        name: 'payinfo',
        component: PayInfo

    },
    {
        path: '/querylist', // 查询
        name: 'query',
        component: QueryInfo
    },

    {
        path: '/gtasks',  //待办
        name: 'Work',
        component: Work
    },
    {
        path: '/company/:id',
        name: 'company',
        component: Company
    },
    {
        path: '/infobase', //信息库
        name: 'database',
        component: Database
    },
    {
        path: '/linklist',    //关联列表
        name: 'linklist',
        component: LinkList
    },
    {
        path: '/linklist/:id',    //关联列表详情
        name:'linklistid',
        component: LinkListself
    },
    {
        path: '/linklistself',    //本单位
        name: 'linklistself',
        component: LinkListself
    },
    {
        path:'/search',              //搜搜索公司详情
        name:'search',
        component: Search

    },
    {
        path:'/serachcompany/:id',
        name: 'serachcompany',
        component:  SearchCom
    }


]
export default new Router({
    // mode: 'history',
    routes
})




