
import MyHeader from '@/components/base/mytop'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: "linklist2",
components:{
MyHeader,
Group,
Cell
},
data(){
return{
frname:'',
xycode: 111,
gdcompany:[],
gduser:[]
}
},
methods:{
getSelfData(){
var index = (this.$route.params.id);
console.log(index)
this.axios.get('http://rap2api.taobao.org/app/mock/22289/getLinkInfoSelf').then((res)=>{
let data = res.data.getSelfInfo;
if(data){
console.log(data)
this.frname = data[index].frname;
this.xycode = data[index].xycode;
this.gdcompany = data[index].gdInfo1.qilist;
console.log(this.gdcompany)
this.gduser = data[index].gdInfo1.qyuser;
console.log(this.gduser);
}
})
}
},
mounted(){
this.getSelfData();
}



}



