
import MyHeader from '../base/mytop'
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

export default {
name: "link-pay",
components: {
XHeader,
MyHeader,
Scroller,
Group,
Cell,
CellBox
},
data(){
return{
datalist:[
{
"id": 1,
"label": "中国人寿关联交易管理系统委托开发合同",
"sponsor": "张三",
"sum": 45,
"type": "信息技术服务"
},
],
pullupConfig:{
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
pulldownConfig:{
content: '下拉刷新',
downContent: '松开进行加载',
upContent: '下拉刷新数据',
loadingContent: '加载中...'
}
}
},
methods:{
goSearch(){
this.$router.push({ path:'/payinfo'})
},
loadMoreData(){},
refresh(){}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.axios.get('http://rap2api.taobao.org/app/mock/22289/getpayinfo').then((res)=>{
console.log(res.data)
vm.datalist = res.data["payinfo"]
})
})
},



}



