import WorkHead from '@/components/work/head.vue'
import WorkRow from '@/components/work/row.vue'
import axios from 'axios'
export default {
  name:'work',

  data(){
    return{
      message:[],
    }
  },
  components: {
    WorkHead,
    WorkRow
  },
  methods:{
    getWorkInfo(){
      axios.get('http://rap2api.taobao.org/app/mock/22289/112')
        .then(this.getWorkInfoSucc)
    },
    getWorkInfoSucc(res){
      res = res.data
      if (res.data){
        this.message=res.data
      }
    }
  },
  mounted (){
    this.getWorkInfo()
  },
}



