
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
props: {
message: Array,
},
components: {
XHeader,
Scroller
},
data () {
return {
keyword: '',
list: [],
timer: null
}
},
computed: {
hasNoData () {
return !this.list.length
}
},
watch: {
keyword () {
console.log(this.message)
if (this.timer) {
clearTimeout(this.timer)
}
if (!this.keyword) {
this.list = []
return
}
this.timer = setTimeout(() => {
const result = []
for (let value in this.message) {
if ( this.message[value].name.indexOf(this.keyword) > -1) {
result.push(this.message[value])
}

}
this.list = result
}, 100)
}
},
methods:{

}
}



