import MyHeader from '@/components/base/mytop.vue'
import DatabaseHead from '@/components/database/head.vue'
import DatabaseSwich from '@/components/database/swich.vue'
import axios from 'axios'
export default {
  data(){
    return{
      company:[],
      people:[],
    }
  },
  components: {
    DatabaseHead,
    DatabaseSwich,
      MyHeader,
  },
    methods:{
    getDatabaseInfo(){
      this.axios.get('http://rap2api.taobao.org/app/mock/22289/200')
        .then(this.getDtabaseInfoSucc)
    },
    getDtabaseInfoSucc(res){
      res = res.data
      if (res){
        const data = res.data

        this.company =data;
        this.people =data.people;

      }
    }
  },
  mounted (){
    this.getDatabaseInfo()
  },
}



