
import XHeader from 'vux/src/components/x-header/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'

export default {
name: "query-info",
components: {
XHeader,
Selector,
Group,
XInput,
XButton,
Box
},
data(){
return{

list:[],
way:[
{
id: '01',
value: '法人/其他组织'
},
{
id:'02',
value: '自然人'
}
]
}
},
methods:{
getCompany(){
this.axios.get('/static/mock/database.json').then((res)=>{
if(res){
let companyArray = res.data.data.company;
let len = companyArray.length
for(let i=0;i<len;i++){
this.list.push(companyArray[i].name)
}

}else{
this.list.push("抱歉，数据找不到了")
}

})

}
},
mounted(){
this.getCompany()

}
}



